import React, { useState } from 'react';

const CourseTop = () => {
    const [isExpanded, setIsExpanded] = useState(false);

    const toggleExpansion = () => {
        setIsExpanded(!isExpanded);
    };

    return (
        <div className="flex flex-col text-grey">
            <h2 className="text-2xl font-700 mb-4 leading-tight">
                What is Python for Kids and Teens?
            </h2>
            <div
                className="text-lg overflow-hidden transition-all duration-700"
                style={{ maxHeight: isExpanded ? 'none' : '80px' }}
            >
                <p>
                    Python is a text-based programming language suitable for kids to learn. It is one of the most popular programming languages. Python is used to develop apps and software. For example, YouTube and Instagram are built using Python.
                </p>
                <p className="flex flex-col space-y-4 mt-4"></p>
                <p>
                    Python is used for web development, game development, building apps, machine learning, and more. <strong>Python Classes For Kids and Teens</strong> will quickly introduce them to principles of coding concepts and boost their analytical skills.
                </p>
                <p>
                    Python programming teaches kids and teens concepts of variables, keywords, operators, loops, if-else statements, conditional statements, functions, recursion, exception handling, list, dictionary, data structures, object-oriented programming, and more. In this course, students will also learn to build a game and GUI from scratch using Python.
                </p>
                <p>
                    Python programming will help kids and teens to get a grip on the basics of programming. Then, as kids explore what Python coding offers, they can learn various coding skills, from problem-solving to analytical thinking, that will grow with them on their coding journey beyond Python. It breaks coding into more easily grasped chunks so children can build their understanding while actively building actual code. It also helps the kids and teens to solve real-world problems using data structures and object-oriented programming in Python.
                </p>
                <p />
            </div>
            <button
                className="underline text-left mt-3 focus:outline-none hover:text-orange"
                onClick={toggleExpansion}
            >
                {isExpanded ? 'Show less' : 'Show more'}
            </button>
        </div>
    );
};

export default CourseTop;
