import React, { useState } from 'react';
import Java from '../../../../assets/Java.webp'

const ModuleContent = ({ moduleNumber, moduleTitle, moduleDescription, lessons, imgSrc }) => {
  const [isContentVisible, setContentVisible] = useState(false);

  const handleToggle = () => {
    setContentVisible(!isContentVisible);
  };

  return (
    <div className="flex flex-col lg:flex-row border border-grey-500 bg-grey-200 bg-opacity-75 select-none cursor-pointer">
      <div className="flex items-center justify-between lg:hidden px-4 py-2" onClick={handleToggle}>
        <p className="text-orange font-semibold">{moduleNumber}</p>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width={35}
          height={24}
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          strokeWidth={2}
          strokeLinecap="round"
          strokeLinejoin="round"
          className={`text-grey transition-transform duration-300 ${isContentVisible ? 'rotate-180' : ''}`}
        >
          <polyline points="6 9 12 15 18 9" />
        </svg>
      </div>
      <div className="lg:flex py-4 lg:pl-5 pr-3 lg:pr-0 lg:space-x-5 lg:items-start">
        <div className="flex flex-col lg:items-center lg:flex-shrink-0">
          <div className="w-20 md:p-0 hidden lg:block">
            <img
              src={imgSrc}
              className="hidden lg:block"
              alt={moduleTitle}
              width="80px"
              height="80px"
            />
          </div>
          <p className="hidden lg:block text-orange font-semibold">{moduleNumber}</p>
        </div>
        <div className="flex flex-col lg:w-full">
          <div className="flex flex-col text-grey space-y-3 leading-tight">
            <div className="flex lg:flex-row flex-col items-start lg:items-center space-y-1 lg:space-y-0 lg:space-x-2">
              <h3 className="text-black font-semibold text-lg">{moduleTitle}</h3>
            </div>
            <div className="relative flex flex-col items-start">
              <div className="overflow-hidden" style={{ height: "auto" }}>
                {moduleDescription}
              </div>
            </div>
          </div>
          <div
            className={`flex flex-col transition-all duration-1000 overflow-hidden ${isContentVisible ? 'max-h-screen' : 'max-h-0'}`}
          >
            <div className="flex flex-col mt-5">
              {lessons.map((lesson, index) => (
                <div key={index} className="py-4 flex space-x-3 border-t border-grey-400 lessonContainer">
                  <div className="flex flex-col items-start space-y-3 leading-tight">
                    <div className="flex lg:flex-row flex-col lg:items-center">
                      <p className="text-lg font-semibold text-grey">{lesson.title}</p>
                    </div>
                    <p>{lesson.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="hidden lg:flex pt-10 pr-5 order" onClick={handleToggle}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width={35}
            height={24}
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            strokeWidth={2}
            strokeLinecap="round"
            strokeLinejoin="round"
            className={`text-grey transition-transform duration-300 ${isContentVisible ? 'rotate-180' : ''}`}
          >
            <polyline points="6 9 12 15 18 9" />
          </svg>
        </div>
      </div>
    </div>
  );
};

const ModuleList = () => {
  const modules = [
    {
      moduleNumber: 'Module 1',
      moduleTitle: 'Python Basics',
      moduleDescription:
        'Get introduced to Python, including data types, operators, and conditional statements, and write basic codes in Python.',
      imgSrc: Java,
      lessons: [
        { title: 'Lesson 1: Welcome to Python programming', description: 'Learn about computer programs, different programming languages, introduction to Python, the history of Python, algorithms, flowcharts, and repl.it platform.' },
        { title: 'Lesson 2: Getting started with programming', description: 'Introduction to print statements, identifiers, keywords, and variables in Python.' },
        { title: 'Lesson 3: Data types in Python', description: 'Learn about data types, typecasting, user input, and string operations.' },
        { title: 'Lesson 4: Python operators I', description: 'Learn about Python operators and the application of various basic operators in Python.' },
        { title: 'Lesson 5: Conditional statements', description: 'Learn about decision-making statements in Python programs. Also, learn to use the if-else statement.' },
        { title: 'Lesson 6: Python operators II', description: 'Learn about conditional statements if, elif, else, and the logical operators used in Python programming.' },
        { title: 'Lesson 7: Python operators III', description: 'Learn about identity operators, membership operators, and bitwise operators and the application of these operators in Python programming.' },
        { title: 'Lesson 8: Python challenge', description: 'Learn to write programs using all the concepts learned in the module.' },
      ],
    },
    {
      moduleNumber: 'Module 2',
      moduleTitle: 'Loops in Python',
      moduleDescription:
        'Learn about conditional statements, understand essential programming concepts, i.e., loops and nested conditional statements, and use Python library Turtle for creating graphics.',
      imgSrc: Java,
      lessons: [
        { title: 'Lesson 1: Nested conditional statements', description: 'Learn about nested conditional statements, the basics of conditional statements, and work on nested conditional statements in Python programming.' },
        { title: 'Lesson 2: Loops', description: 'Learn about programming loops and their types, the use of loops in programs, and how to work with them to solve various problems.' },
        { title: 'Lesson 3: While loop', description: 'Get introduced to the while loop, how and when to use them in your Python program.' },
        { title: 'Lesson 4: Nested loop', description: 'Learn about nested loops, how to use nested loops, and the syntax of the nested loops in the Python program.' },
        { title: 'Lesson 5: Patterns', description: 'Get introduced to nested loop‚Äôs application and how to make exciting patterns through nested loops in Python programming.' },
        { title: 'Lesson 6: Introduction to turtle', description: 'Learn about Turtle library in Python to create graphics and learn some essential functions used in the Turtle library.' },
      ],
    },
    {
        moduleNumber: 'Module 3',
        moduleTitle: 'Loops in Python',
        moduleDescription:
          'Learn about conditional statements, understand essential programming concepts, i.e., loops and nested conditional statements, and use Python library Turtle for creating graphics.',
        imgSrc: Java,
        lessons: [
          { title: 'Lesson 1: Nested conditional statements', description: 'Learn about nested conditional statements, the basics of conditional statements, and work on nested conditional statements in Python programming.' },
          { title: 'Lesson 2: Loops', description: 'Learn about programming loops and their types, the use of loops in programs, and how to work with them to solve various problems.' },
          { title: 'Lesson 3: While loop', description: 'Get introduced to the while loop, how and when to use them in your Python program.' },
          { title: 'Lesson 4: Nested loop', description: 'Learn about nested loops, how to use nested loops, and the syntax of the nested loops in the Python program.' },
          { title: 'Lesson 5: Patterns', description: 'Get introduced to nested loop‚Äôs application and how to make exciting patterns through nested loops in Python programming.' },
          { title: 'Lesson 6: Introduction to turtle', description: 'Learn about Turtle library in Python to create graphics and learn some essential functions used in the Turtle library.' },
        ],
      },
  ];

  return (
    <div>
      {modules.map((module, index) => (
        <ModuleContent
          key={index}
          moduleNumber={module.moduleNumber}
          moduleTitle={module.moduleTitle}
          moduleDescription={module.moduleDescription}
          lessons={module.lessons}
          imgSrc={module.imgSrc}
        />
      ))}
    </div>
  );
};

export default ModuleList;
