import React from 'react';

// Component for course features with Roman numerals
const Feature = ({ number, text }) => (
  <div className="flex items-center space-x-2">
    <span className="font-bold">{number}.</span>
    <p>{text}</p>
  </div>
);

// Main FixedContent component
const FixedContent = () => {
  const features = [
    { text: "44 live private 1-on-1 sessions of 45 mins each" },
    { text: "Learn live with expert Computer Science teacher" },
    { text: "7 detailed modules" },
    { text: "100+ activities & projects" },
    { text: "STEM.org accredited certificate" },
    { text: "Lifetime access to course content" },
    { text: "Unlimited 1:1 live doubt sessions with instructors" },
    { text: "Live 1:1 class with Coding Instructor" },
    { text: "Join 10,000+ happy students" },
  ];

  return (
    <div className="pb-5 sticky top-0 pt-5 z-40 w-80">
      <div className="flex bg-white flex-col md:shadow-lg relative md:w-80 mt-12">
        <div className="flex flex-col m-5 text-grey text-sm">
          <button className="inline-block border rounded font-700 px-6 lg:px-10 py-2 h-11 bg-green-500 text-red hover:bg-green-500 active:bg-green-800 w-full lg:w-auto hidden lg:block">
            Try a free class
          </button>

          <a
            href="https://www.codingal.com/pdfs/courses/v7/codingal-python-curriculum.pdf"
            target="_blank"
            rel="noreferrer"
            className="hidden lg:flex text-orange mt-3 items-center justify-center bg-blue-500 p-2"
          >
            Download Curriculum
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width={14}
              height={14}
              fill="none"
              stroke="currentColor"
              strokeWidth={2}
              className="ml-2"
            >
              <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
              <polyline points="7 10 12 15 17 10" />
              <line x1={12} y1={15} x2={12} y2={3} />
            </svg>
          </a>

          <p className="text-lg font-700 mt-5">This course includes:</p>
          <div className="space-y-2 mt-3">
            {features.map((feature, index) => (
              <Feature key={feature.text} number={`${index + 1}`} text={feature.text} />
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default FixedContent;
