import React, { useState } from 'react';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa'; // Ensure you have react-icons installed

const FAQ = () => {
  const [openFAQ, setOpenFAQ] = useState(null);

  const toggleFAQ = (index) => {
    setOpenFAQ(openFAQ === index ? null : index);
  };

  const faqs = [
    {
      question: "What are Python games for kids?",
      answer: "Kids can learn how to code in Python by creating multiple interactive and fun games. Some popular Python games for kids include Ping pong, Rock paper scissors, Snake game, Tic-Tac-Toe, etc. Python games help kids learn Python and explore new libraries of Python.",
    },
    {
      question: "What are Loops in Python?",
      answer: "A loop statement in Python programming allows you to run a statement or group of statements multiple times. For example, the ATM software is in a loop to process transaction after transaction until you acknowledge that you have no more to do. Sometimes, we are required to execute a code several times. So there, we use the concept of loops.",
    },
    {
      question: "What are Python functions?",
      answer: "A function is a block of codes of related statements that performs a specific task. Functions help break our program into small blocks of code. As the program grows larger and larger, functions make it more organized and manageable. Suppose repeated code occurs in a program. The function can include those codes and execute them when needed by calling that function. Programmers working on large projects can divide the workload by making different functions.",
    },
    {
      question: "What is Exception Handling in Python?",
      answer: "An exception can be defined as an unusual condition in a program resulting in an interruption in the flow of the program. Whenever an exception occurs, the program stops the execution, and thus the other code is not executed. An exception is an object that represents the error. Python provides a way to handle the exception so that c can execute the code without any interruption.",
    },
    {
      question: "What are Python Data Structures?",
      answer: "Data Structures help you in storing the collection of data, managing it, and performing operations on them. Data Structures help you to make modifications easier on collections of data. In Python, we have four Data Structures- List, Dictionary, Tuple, and Set.",
    },
  ];

  return (
    <div className="container mx-auto mt-16">
      <h2 className="text-center text-xl md:text-3xl font-bold mb-10 leading-tight">
        Frequently Asked Questions
      </h2>
      <div className="flex flex-col gap-4">
        {faqs.map((faq, index) => (
          <section key={index} className="border-b pb-2">
            <div
              className="flex justify-between items-center cursor-pointer"
              onClick={() => toggleFAQ(index)}
            >
              <h3 className="font-semibold text-lg">{faq.question}</h3>
              {openFAQ === index ? <FaChevronUp /> : <FaChevronDown />}
            </div>
            {openFAQ === index && <p className="mt-2">{faq.answer}</p>}
          </section>
        ))}
      </div>
    </div>
  );
};

export default FAQ;
